.popup__close-button {
    position: absolute;
    margin: 0;
    padding: 0;
    border-style: none;
    width: 32px;
    height: 32px;
    background-color: transparent;
    background-image: url(../../../images/popupCloseButton.svg);
    background-size: cover;
    background-repeat: no-repeat;
    transition: opacity .3s linear;
    transform: translate(100%, -100%);
    top: -8px;
    right: -8px;
}
  
.popup__close-button:hover {
    opacity: .6;
    cursor: pointer;
}