.popup {
    position: fixed;
    display: flex;
    visibility: hidden;
    justify-content: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.5);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    opacity: 0;
    transition: all 0.5s ease-in;
    z-index: 0;
  }